.App {
 position: relative;
}
.meme h4.top {
  position: relative;
  margin: 0;
  padding: 0;
  top: 25px;
  text-shadow: 2px 2px 2px #fff;
  text-transform: uppercase;
  font-weight: bolder;
  z-index: 10;
  opacity: 1;
  text-align: center;
}

form.formSection {
  padding: 40px 10px;
}

.flex {
  display: flex;
}

.divSections {
  padding: 0px 30px 0px 0px;
}

input[type="text"] {
  padding: 6px;
  border-radius: 2px;
  border: 1px solid #ccc;
}

.divSections label {
  padding: 0px 10px 0px 0px;
}

.meme img {
  width: 200px;
  z-index: 8;
  opacity: 0.99;
  padding: 7px 14px;
}

.meme h4.bottom {
  position: relative;
  bottom: 25px;
  padding: 0;
  margin: 0px;
  text-shadow: 2px 2px 2px #fff;
  text-transform: uppercase;
  font-weight: bolder;
  z-index: 10;
  opacity: 1;
  text-align: center;
}

.memeHeader {
  padding: 20px;
  text-align: center;
  background: #004eff;
  color: #fff;
  font-size: 27px;
  font-weight: 500;
}

button.btn.btn-info {
  background: #004eff;
  border: 1px solid #004eff;
  padding: 7px 14px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  position: absolute;
  bottom: -35px;
}

.meme {
  width: 200px;
}

/* Media Query */
@media (min-width:300px) and (max-width:798px)
{
  form.formSection {
    padding: 3px 10px;
}
.divSections label {
  padding: 10px 0px 5px 0px;
  display: block;
}
.flex {
  display: block;
}
}